.navbar
{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
    align-items: center;
}
.nav-logo
{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color: darkolivegreen;
    font-size: 38px;
    font-weight:600;
}
.nav-menu
{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: rgb(69, 69, 69);
    font-size: 20px;
    font-weight: 500;
}
.nav-menu li{
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   gap: 3px;
   cursor: pointer; 

 
}

.nav-menu hr
{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background-color: rgb(0, 80, 30);
}
.nav-login-cart
{
    display: flex;
    align-items: center;
    gap: 45px;
}
.nav-login-cart button{
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid  grey;
    border-radius: 75px;
    color: grey;
    font-size: 20px;
    font-weight:500;
    background-color: rgb(255, 255, 255);
    cursor: pointer;
}
.nav-login-cart button:active{
    background-color: rgb(139, 176, 137);
}
.nav-cart-count
{
    width:22px;
    height: 22px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: -55px;
    margin-top: -35px;
    border-radius: 11px;
    font-size: 14px;
    background-color: red;
    color: white;
}
.nav-dropdown{
    display: none;
}
@media ( max-width:1280px)
{
    .navbar{
        padding: 12px 50px;
    }
    .nav-logo img{
width: 40px;
    }
    .nav-logo p{
        font-size: 25px;
    }
    .nav-menu{
        gap: 30px;
        font-size: 16px;
    }
    .nav-login-cart{
        gap: 30px;
    }
    .nav-login-cart button{
        width: 120px;
        height: 45px;
        font-size: 16px;
    }
    .nav-cart-count
    {
        margin-left: -40px;
        font-size: 12px;
    }
}
@media (max-width:1024px) {
    .navbar{
        padding: 12px 30px;
    }
    .nav-menu{
        gap: 25px;
        font-size: 14px;
    }
    .nav-login-cart button
    {width: 80px;
    height: 35px;
font-size: 14px;}
.nav-login-cart img{
    width: 30px;
    
}
.nav-cart-count{
    width: 18px;
    height: 18px;

}
}
@media (max-width:800px)
{
    .navbar{
        padding: 10px 0px;

    }
    .nav-dropdown{
display: block;
width: 30px;
rotate:-90deg;
transition: 0.5s;
    }
    .nav-menu{
        display: none;
        height: 80px;
        width: 100%;
        position: absolute;
        background-color: white;
        justify-content: center;
        top: 50px;
    }
    .nav-menu-visible{
        display: flex;
    }
    .nav-dropdown.open{
        transform: rotate(90deg);
    }
}
@media(max-width:500px)
{
    .navbar{
        padding: 8px 0px;
        gap: 0px;
    }
    .nav-logo{
        transform: scale(0.8);
    }
    .nav-menu{
        height: 70px;
        top: 50px;
    }
    .nav-login-cart{
        transform: scale(0.8);
    }
}