.productdisplay
{
    display: flex;
    margin: 0px 170px;
}
.productdisplay-left
{
    display: flex;
    gap: 17px;
}
.productdisplay-img-list
{
    display: flex;
    flex-direction: column;
    gap: 16px;
}
.productdisplay-img-list img
{
height: 163px;
}
.productdisplay-main-img
{
    width: 586px;
    height: 700px;
}
.productdisplay-right
{
    margin: 0px 70px;
    display: flex;
    flex-direction: column;

}
.productdisplay-right h1{
    color:black;
    font-size: 40px;
    font-weight: 700;
}
.productdisplay-right-stars
{
    display: flex;
    align-items: center;
    margin-top: 13px;
    gap: 5px;
    color: rgba(178, 0, 0, 0.679);
    font-size: 16px;
    
}
.productdisplay-right-prices
{
    display: flex;
    margin: 40px 0px;
    gap: 30px;
    font-size: 24px;
    font-weight: 700;
}
.productdisplay-right-price-old
{ color: rgb(79, 79, 79);
    text-decoration: line-through;
}
.productdisplay-right-price-new
{ color: red;
    
}
.productdisplay-right-size h1{
    margin-top: 20px;
    color: darkolivegreen;
    font-size: 20px;
    font-weight: 600;
}
.productdisplay-right-sizes
{
    display: flex;
    margin: 30px 0px;
    gap: 20px;
}
.productdisplay-right-sizes div{
    padding: 18px 24px;
    background-color: white;
    border: 1px solid rgb(255, 144, 144);
    border-radius: 3px;
    cursor: pointer;

}
.productdisplay-right button{
    padding: 20px 40px;
    width: 200px;
    font-size: 16px;
    font-weight: 600;
    color: white;
    background: green;
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;
}
.productdisplay-right-category
{
    margin-top: 10px;
}
.productdisplay-right-category span{
    font-weight: 600;
}
#popup{
    position: absolute;
    top:50%;
    left: 50%;
    background-color: white;
    padding: 2%;
    opacity: 80%;
    border-radius: 20px;
    display: none;
    text-align: center;
}