.relatedproducts
{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 90vh;
    gap: 10px;
}
.relatedproducts h1{
    color: rgb(0, 121, 81);
    font-size: 50px;
    font-weight: 600;
}
.relatedproducts hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background:#252525 ;
}
.relatedproducts-item
{
    margin-top: 50px;
    display: flex;
    gap: 30px;
}