.cartitems{
   margin:100px 170px; 
}
.cartitems hr{
    height: 1px;
    background-color: rgb(162, 162, 162);
    border: 0;
}
.cartitems-format-main{
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 75px;
    padding: 20px 0px;
    color: rgb(58, 58, 58);
    font-size: 18px;
    font-weight: 600;
}
.cartitems-format{
    font-size: 17px;
    font-weight: 500;

}
.carticon-product-icon
{
    height: 62px;
}
.caritems-remove-icon
{
width: 15px;
margin: 0px 40px;
cursor: pointer;
}
.cartitems-quantity
{
    width: 64px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: rgb(220, 202, 202);
}
.cartitems-down
{
    display: flex;
    margin: 100px 0px;
}
.cartitems-total
{flex: 1;
display: flex;
flex-direction: column;
margin-right: 200px;
gap: 40px;}
.cartitems-total-item
{
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
}
.cartitems-total button{
    width: 262px;
    height: 58px;
    outline: none;
    border: none;
    background: indianred;
    color: white;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}
.cartitems-promocode
{
    flex:1;
    font-size: 16px;
    font-weight: 500;

}
.cartitems-promocode p{
    color: rgb(14, 87, 46);
}
.cartitems-promobox
{
    width: 504px;
    margin-top: 15px;
    margin-top: 15px;
    padding-left: 20px;
    height: 58px;
    background: #eaeaea;
}
.cartitems-promobox input{
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    width: 330px;
    height: 50px;

}
.cartitems-promobox button
{
    width: 170px;
    height: 58px;
    font-size: 16px;
    background:rgb(0, 0, 0);
    color: white;
    cursor: pointer;
}