.item
{
    width: 280px;

}
.item img{
    width: 280px;
    height:500px;
}
.item p{
    margin: 6px 0px;
   
}
.item-prices
{
    display: flex;
    gap: 20px;
}
.item-price-new
{
    color: darkolivegreen;
    font-size: 18px;
    font-weight: 600;
}
.item-price-old
{
    color: green;
    font-size:18px ;
    font-weight: 500;
    text-decoration: line-through;
}
.item:hover
{
    transform: scale(1.05);
    transition: 0.6s;
}
@media (max-width:1280px) {
    .item{
        width: 220px;
        font-size: 14px;
    }
    .item img{
        width: 220px;
        height: 400px;
    }
    .item-price-old{
        font-size: 14px;
    }
}
@media(max-width:1024px)
{
    .item{
        width: 170px;
        font-size: 13px;
    }
    .item img{
        width: 170px;
        height: 300px;
    }
    .item-price-old{
        font-size: 13px;
    } 
}
@media(max-width:800px)
{
    .item{
        width: 120px;
        font-size: 12px;
    }
    .item img{
        width: 120px;
        height: 200px;
    }
    .item-price-old{
        font-size: 12px;
    } 
}
@media(max-width:500px)
{
    .item{
        width: 160px;
    }
    .item img{
        width: 160px;
        height: 200px;
    }
    .item-price-old{
        font-size: 12px;
    } 
}